@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;900&display=swap');

*, *::after, *::before {
  margin: 0; padding: 0;
  box-sizing: border-box;
}

:root {
  --color-blue: #002581;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-size: 18px;
  color: var(--color-blue);
}

main {
  overflow: hidden;
}

.container {
  margin: 0 10vw;
}

.ui-loader {
  display:none !important;
  visibility: hidden !important;
}

a {
  color: inherit;
  text-decoration: none;

  &.button {
    display: block;
    width: 100%; max-width: 550px;
    padding: .5em;

    font-size: 1.5em;
    color: #ffffff;
    font-weight: 1000;
    text-align: center;

    background-color: var(--color-blue);
    border-radius: 2em;

    margin-top: 1em;
  }
}

.text {

  &-center {
    text-align: center;
  }

  p {
    line-height: 1.5em;
    margin-bottom: 1em;
  }
  
  ul {
    list-style: 'â€“';
    margin-bottom: 1em;
  
    li {
      margin-bottom: .3em;
      margin-left: 2em;
    }
  }
  
  ol {
    margin-bottom: 1em;
  
    li {
      margin-bottom: .3em;
      margin-left: 2em;
    }
  }
}

.preview {
  color: #ffffff;
  overflow: hidden;
  position: relative;

  &__screen {
    width: 100vw;
    height: 81vh;
    position: relative;
    
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      filter: brightness(85%);
    }

    &-big {
      height: 100vh;
    }
    
    &-shmol {
      height: 50vh;
    }
    
    &-title {
      max-width: 850px;
      position: absolute;
      left: 10vw;
      bottom: 10vw;
      
      font-size: 2.5em;

      &-big {
        font-size: 1.5em;
        font-weight: 1000;
      }
    }
  }

  .header {
    position: absolute;
    top: 0;
    width: 100%;

    background-color: rgba($color: #000000, $alpha: 0.5);

    .container {
      padding: 1em 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    img {
      height: 4.5em;
    }

    &__menu {
      display: flex;
      gap: 1vw;

      &-item {
        list-style: none;
        text-transform: uppercase;
        cursor: pointer;
      }
    }
  }
}

.footer {
  color: #ffffff;

  &__first {
    padding: 4em 0;
    background-color: #3D3D3D;

    .container {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &-right {
      &-title {
        font-weight: 1000;
        font-size: 2.5em;
        text-transform: uppercase;
        margin-bottom: 0.5em;
      }

      &-contact {
        text-align: center;

        div {
          margin-bottom: .5em;
        }
      }
    }

    &-left {
      display: flex;
      align-items: center;
      gap: 10vw;

      img {
        height: 7em;
      }

      &-menu {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: .5em;

        font-size: 1.2em;

        list-style: none;
        text-transform: capitalize;
      }
    }
  }

  &__second {
    padding: 2em 0;
    background-color: #232323;
    
    .container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      
      div {
        display: flex;
        align-items: center;
        gap: 1em;
      }

      img {
        height: 2em;
      }
    }
  }
}

.block-list {
  background-color: var(--color-blue);
  color: #ffffff;

  .container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }

  &__item {
    position: relative;
    border: 1px solid rgba($color: #ffffff, $alpha: 0.2);

    &::before {
      content: "";
      display: block;
      width: 100%;
      padding-bottom: 100%;
    }

    &-content {
      position: absolute;
      top: 50%; left: 50%;
      transform: translate(-50%, -50%);
      max-width: 50%;

      text-align: center;
      text-transform: uppercase;

      img {
        margin-bottom: 1em;
        max-width: 100%;
      }
    }
  }
}

.flex-block {
  display: flex;
  gap: 3em;
}

.title {
  font-size: 3em;
  font-weight: 1000;
  text-transform: uppercase;
  text-align: center;

  margin-bottom: 1em;
}

.background-image {
  position: absolute;
  max-height: 150vh;
  max-width: 90vw;

  z-index: -1;

  opacity: .1;
}

.block {
  position: relative;
  margin: 3em 0;

  &-home {
    .container{
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 1.5em;
      align-items: center;
    }

    img {
      width: 100%;
      border-radius: 2.5em;
    }

    &__content {

      &-title {
        font-weight: 1000;
        margin-bottom: 1em;
      }

      &-text {
        margin-left: 2em;
        text-align: justify;
      }
    }
  }

  &-rafting {

    &__content {

      &-subtitle {
        font-size: 3em;
        font-weight: 1000;
        margin-bottom: .5em;
      }

      &-text {
        list-style: inside;
      }
    }

    &__description {
      .background-image {
        top: -5%; left: -20%;
      }

      &-content {
        display: grid;
        gap: 5em;

        &-text {

          &-center {
            text-align: center;
          }

          &-images {
            display: grid;
            grid-template-columns: 2fr 1fr;
            gap: 3em;

            p {
              margin-bottom: 2em;
            }

            img {
              width: 100%;
            }
          }
        }

        &-subtitle {
          font-size: 3em;
          font-weight: 1000;
          text-align: center;
        
          margin-bottom: 1em;
        }
      }
    }

    &__details {
      .background-image {
        top: 0; right: -20%;
      }
      .container {
        & > div {
          margin-bottom: 3em;
        }
      }
      p {
        line-height: normal;
      }
    }
  }

  &-jeeping {
    
    &__description {
      .background-image {
        bottom: 0; right: -10%;
      }

      &-content {
        &-blue {
          background-color: var(--color-blue);

          padding: 2em 0;

          .subtitle {
            font-size: 2em;
            font-weight: 1000;
            color: #ffffff;
          }
        }
      }
    }

    &__slider {

      &-content {
        height: 550px;
        width: 100%;

        .slideShow {
          height: 100%;
          width: 50%;
          min-width: 750px;
          margin: 0 auto;
          
          &__container {
            height: 100%;
            width: 100%;
            list-style: none;
            white-space: nowrap;
            position: relative;
          }
          
          &__slide {
            height: 100%;
            width: 100%;
            display: inline-block;
            position: relative;
          }
          
          &__image {
            display: block;
            margin: 0;
            padding: 0;
            height: 90%;
            width: 100%;
            object-fit: cover;
            position: relative;
            transform: scale(0.8);

            img {
              width: 100%; height: 100%;
            }
            
            &::after {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              background-image: linear-gradient(225deg, rgba(0,0,0,0.00) 0%, rgba(0,0,0,0.5) 100%);
            }
          }
          
          &__title {
            padding: 1em;
            opacity: 0;

            font-size: 1.5em;
            font-weight: 1000;
            text-align: center;
          }
          
          &__next,
          &__prev {
            position: absolute;
            height: 100%; width: 25%;
            top: 0;
            cursor: pointer;
          }
          
          &__next {
            right: 0;
          }
          
          &__prev {
            left: 0;
          }
        }
      }
    }

    &__gallary {

      .background-image {
        top: 50%; left: 50%;
        width: 90vw;
        transform: translate(-50%, -50%);
      }
      
      &-content {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 1em;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
  }

  &-transfer {
    
    &__description {
      .background-image {
        bottom: 0; right: -10%;
      }

      &-content {

        &-text {
          &-min {
            max-width: 850px;
          }

          &-center {
            text-align: center;
            margin-top: -1em;
            margin-bottom: 3em;
          }
        }
      }
    }
  }

  &-hotel {
    &__price {
      .container {
        position: relative;
      }

      .background-image {
        top: 50%; right: 0;
        transform: translateY(-50%);

        height: 150%;
      }
    }

    &__location {
      &-content-text {
        display: flex;
        gap: 2em;

        &-destination {
          padding: 2em;
          border: 1px solid var(--color-blue);
          border-radius: 2em;
        }
      }
    }

    &__gallary {

      .background-image {
        top: 50%; left: -10%;
        transform: translateY(-50%) scaleX(-1);
      }

      &-content {
        display: grid;
        justify-content: center;
        gap: 1em .2em;
        grid-template-columns: repeat(auto-fill, 160px);

        img {
          width: 100%;
        }
      }
    }
  }

  &-hiking {

    &__price {
      .container {
        position: relative;
      }

      .background-image {
        top: 50%; right: -20%;
        transform: translateY(-50%);

        height: 150%;
      }

      .center {
        text-align: center;

        .button {
          margin: 0 auto;
        }
      }
    }

    &__routes {

      .text-center {
        margin-top: -2em;
      }

      &-gallary {
        margin-top: 2em;
        display: flex;
        flex-wrap: wrap;
        gap: 2vw;
        justify-content: center;

        &-item {
          position: relative;
          overflow: hidden;

          &::before {
            content: "";
            display: block;
            padding: 30%;
          }

          &-primary {
            width: calc(50% - 1vw);

            span {
              bottom: 2em;
              left: 2em;
            }
          }

          &-secondary {
            width: calc(33% - 1.2vw);

            span {
              bottom: 1em;
              left: 1em;
            }
          }
  
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;

            position: absolute;
            top: 0;

            filter: brightness(75%);
            transition: all .2s ease-out;
          }

          span {
            position: absolute;

            color: #ffffff;
            font-weight: 1000;
            font-size: 1.3em;
          }

          &:hover {
            img {
              transform: scale(105%);
              filter: brightness(70%);
            }
          }
        }
      }
    }
  }

  &-contact {
    
    &__content {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 2vw;
      align-items: center;

      form {
        display: grid;
        grid-template-rows: repeat(5, 1fr);
        gap: 1em;

        &>* {
          padding: 1em;

          outline: none;
          border: 1px solid var(--color-blue);
          border-radius: 2em;

          &::placeholder {
            color: var(--color-blue);
            text-align: center;
            text-transform: uppercase;
          }
        }
        
        textarea {
          resize: none;

          grid-row-start: 4;
          grid-row-end: 6;
        }

        button[type="submit"] {
          display: block;
          width: 100%;
          padding: .5em;
      
          font-size: 1.2em;
          color: #ffffff;
          font-weight: 1000;
          text-align: center;
      
          background-color: var(--color-blue);
          border-radius: 2em;
      
          margin-top: 1em;
        }
        
      }

      &-info {
        
        &-icons {
          display: grid;
          gap: .5em;

          &>* {
            display: flex;
            gap: 1em;
            align-items: center;

            img {
              width: 7vh;
              height: 7vh;
              object-fit: contain;
            }

            p {
              margin: 0;
            }
          }
        }
      }
    }
  }
}
